import Vue from 'vue'
import App from './App'
import router from './router'
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css'
Vue.use(Mint);
//1.2.1:()
// Vue.http.options.root="http://127.0.0.1:3000/";
//1.2.2:
Vue.filter("datetimeFilter",function(val){
  var date=new Date(val);
  var y=date.getFullYear();
  var m=date.getMonth()+1;
  m<10&&(m="0"+m);
  var d=date.getDate();
  d<10&&(d="0"+d);
  var h=date.getHours();
  h>12&&(h-=12);
  h<10&&(h="0"+h);
  var M=date.getMinutes()
  M<10&&(M="0"+M);
  return `${y}-${m}-${d} ${h}:${M}`;
});

// mui  https://blog.csdn.net/qq_43021088/article/details/86646388
import './assets/mui/css/mui.css'
import './assets/mui/css/icons-extra.css'
import mui from './assets/mui/js/mui.js'
Vue.prototype.mui=mui;

// axios  https://blog.csdn.net/AKindOf/article/details/89452911
import axios from 'axios'
Vue.prototype.$http=axios

import "@/assets/css/base.css"//

Vue.config.productionTip = false

//2.1:vuex
//vuex
import Vuex from "vuex";
//vuesvue
Vue.use(Vuex);
//vuex[;;]
//new Vuex.Store();
var store=new Vuex.Store({
  state:{//data
    // this.$store.state.***
    //#
    count:1,
    counts:0
  },
  mutations:{
    //#  state mutations
    //mutations 
    //this.$store.commit("increment");
    numUpdate(state){
      state.counts=state.counts+state.count;
      state.count=1
    },
    increment(state){state.count++},
    subtract(state){
      if(state.count>1){
        state.count--
      }
    }//incrementsubtract
  },
  getters:{//getters
    //#getters  
    //{{$store.getters.optCount}}
    optCount:function(state){
      return state.count;
    },
    optCounts:function(state){
      return state.counts
    }
  }
});


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,//Vue
  components: { App },
  template: '<App/>'
})



