<script>
export default {
  data() {
    return {
      phoneAccount:'13632756286',
      timer:'',
      time:60,
      send:true,
      security:'',
      newPass:'',
      newPass2:'',
      place:[],
      projectName:[
        {
          name:'',
          equityStructure:[
            {company:'',stock:'25%'},
            {company:'',stock:'25%'},
            {company:'',stock:'32.5%'},
            {company:'()',stock:'17.5%'}
          ],
          record:[
            {A:'',date:'2019-09-09',B:'',stock:'0.01%'}
          ]
        },
        {
          name:'',
          equityStructure:[
            {company:'',stock:'25%'},
            {company:'',stock:'25%'},
            {company:'',stock:'32.5%'},
            {company:'()',stock:'17.5%'}
          ],
          record:[
            {A:'',date:'2019-09-09',B:'',stock:'0.01%'}
          ]
        },
        {
          name:'',
          equityStructure:[
            {company:'',stock:'25%'},
            {company:'',stock:'25%'},
            {company:'',stock:'32.5%'},
            {company:'()',stock:'17.5%'}
          ],
          record:[
            {A:'',date:'2019-09-09',B:'',stock:'0.01%'}
          ]
        }      
      ],
      boxShadow0:'',
      boxShadow1:'',
      boxShadow2:'',
      door1: 'down',
      door2: 'down',
      door3: 'down', 
      order: 4,
      myapp: '',
      mytitle: '',
      showTitle:false,
      door: 'close',
      people: true,
      doors:[],
      piece: 0,
      options: [{
        title: '-',
        time: '2019-04-24',
        serial: 'LSZY-SZ-002',
        name: '',
        location: ''
      }, {
        title: '-',
        time: '2019-04-24',
        serial: 'LSZY-SZ-002',
        name: '',
        location: ''
      }, {
        title: '-',
        time: '2019-04-24',
        serial: 'LSZY-SZ-002',
        name: '',
        location: ''
      }, {
        title: '-',
        time: '2019-04-24',
        serial: 'LSZY-SZ-002',
        name: '',
        location: ''
      }],
      options2: [{
        company: '',
        stock: '25%'
      },{
        company: '',
        stock: '25%'
      },{
        company: '',
        stock: '25%'
      },{
        company: '',
        stock: '25%'
      }],
      options3: [{
        title: '4',
        name: '',
        time: '2019-06-31 09:30',
        month: '4 300,000,00',
        stock: '32.5% 97.500.00'
      },{
        title: '3',
        name: '',
        time: '2019-06-31 09:30',
        month: '4 300,000,00',
        stock: '32.5% 97.500.00'
      },{
        title: '2',        
        name: '',
        time: '2019-06-31 09:30',
        month: '4 300,000,00',
        stock: '32.5% 97.500.00'
      },{
        title: '1',        
        name: '',
        time: '2019-06-31 09:30',
        month: '4 300,000,00',
        stock: '32.5% 97.500.00'
      }]
    }
  },
  beforeMount() {
    for(let i=0;i<this.options.length;i++){
      this.doors.push({door: 'down',arrow: true});
    }

    for(let i=0;i<this.projectName.length;i++){
      this.place.push({pitchOn1:'pitchOn',pitchOn2:'',isShow:true});
    }
  },
  mounted() {
    this.jump(3,0);
  },
  methods: {
    myDoor() {
      if(this.door == "open"){
        this.door = "close";
        this.people = true
      }else{
        this.door = 'open';
        this.people = false
      }
    },
    openDoor(index){
      // console.log(index);
      if(this.doors[index].door == 'up'){
        this.doors[index].door = 'down';
        this.doors[index].arrow = true;
      }else{
        this.doors[index].door = 'up';
        this.doors[index].arrow = false;
      }
    },
    // closeDoor(index,num){
    //   if(num == 0){
    //     if(this.door1 == 'up2'){
    //       this.door1 = 'down';
    //     }else{
    //       this.door1 = 'up2';
    //     }
    //   }else if(num == 1){
    //     if(this.door2 == 'up2'){
    //       this.door2 = 'down';
    //     }else{
    //       this.door2 = 'up2';
    //     }
    //   }else{
    //     if(this.door3 == 'up2'){
    //       this.door3 = 'down';
    //     }else{
    //       this.door3 = 'up2';
    //     }
    //   }
    //   this.jump(index);
    // },
    jump(index,num){
      this.order = index;
      this.door = 'close';
      this.people = true;
      this.piece = num;
      this.showTitle = false;
      if(this.order == 2){
        this.myapp = 'app appbg3';
        this.mytitle = '';
      }else if(this.order == 1){
        this.myapp = 'app appbg3';
        this.mytitle = '';        
      }else if(this.order == 0){
        this.myapp = 'app appbg1';
        this.mytitle = '';
        this.mytitle = num == 0 ? '' : (this.mytitle = num == 1 ? '' : '');
      }else if(this.order == 4){
        this.myapp = 'app appbg1';
        this.mytitle = '';
      }else if(this.order == 5){
        this.myapp = 'app appbg3';
        this.mytitle = '';
      }else if(this.order == 6){
        this.myapp = 'app appbg3';
        this.mytitle = '';
      }else if(this.order == 7){
        this.myapp = 'app appbg1';
        this.mytitle = '';
      }else{
        this.showTitle = true;
        this.myapp = 'app';
        this.$router.push("/home");
      };
      this.boxShadow0 = this.boxShadow1 = this.boxShadow2 = "";
      if(index == 0){
        this.boxShadow0 = 'boxShadow'
      }else if(index == 1){
        this.boxShadow1 = 'boxShadow'
      }else if(index == 2){
        this.boxShadow2 = 'boxShadow'
      }
    },
    pitchOn(index,num){
      if(num == 0){
        this.place[index].pitchOn1 = 'pitchOn';
        this.place[index].pitchOn2 = '';
        this.place[index].isShow = true;
      }else{
        this.place[index].pitchOn1 = '';
        this.place[index].pitchOn2 = 'pitchOn';
        this.place[index].isShow = false;
      }
    },
    countDown() {
      this.send = false;
      this.security = '';
      this.time = 60;
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        if(this.time > 0){
          this.time--;
          // console.log(this.time);
        }else{
          clearInterval(this.timer);
          this.timer=null;
          this.send = true;
          this.time = 60;
          // console.log('???');
        }
      },1000);
    },
    resetPass() {
      if((/^\w{6}$/).test(this.security)){
        if((/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_\.-@]{6,15}$/).test(this.newPass)){
          if(this.newPass == this.newPass2){
            this.isShow = 4;
            this.newPass = this.newPass2 = '';
            this.$toast({
                message:"",
                position:"center",
                iconClass: 'mintui mintui-success',
                duration:2000
            });
          }else{
            this.newPass = this.newPass2 = '';
            this.$toast({
              message:"",
              position:"center",
              iconClass: 'mintui mintui-success',
              duration:2000
            });
          }
        }else{
          this.$toast({
              message:"6-15",
              position:"center",
              duration:2000
          });  
        }
      }else{
        this.$toast({
            message:"6",
            position:"center",
            duration:2000
        });  
      }
    }
  }
}
</script>

