    padding-top:10px;
  }

  .myApp>ul>li>ul>li:last-child>button{
    border-radius:50px;
    font-size:12px;
    margin:5px 0 10px;
  }

  .myApp>ul>li>ul{
    overflow: hidden;
  }

  .down{
    transition:all .3s linear;
    height:0;
  }

  .up{
    transition:all .3s linear;
    height:200px;
    border-top:1px solid #fff; 
  }

  .up2{
    transition:all .3s linear;
    height:80px;
    border-top:1px solid #fff; 
  }

  .mui-icon{
    font-size:12px;
  }

  .myApp>ul{
    padding-bottom: 100px;
  }

  .myApp>ul>li>span{
    font-size:14px;
  }

  /* && */
  .myApp>ul:nth-child(3)>li>ul>li,.myApp>ul:nth-child(4)>li>ul>li{
    display: flex;
    justify-content: space-between;
  }

  .myApp>ul:nth-child(3)>li>ul,.myApp>ul:nth-child(4)>li>ul{
    padding-bottom:10px;
  }

  .myApp>ul:nth-child(3)>li>span,.myApp>ul:nth-child(4)>li>span{
    /* border-bottom: 1px solid #fff; */
    padding-bottom: 5px;
  }

  .myMain>section>section:nth-child(4)>ul,.myApp>ul:nth-child(3)>li>ul,.myApp>ul:nth-child(3)>li>span{
    padding-bottom: 0;
  }

  /*  */
  .boxShadow{
    box-shadow: 0px 2px 5px 0px #000 inset;
  }

  /*  */
  .stock{
    height: 667px;
    width: 100%;
    color: #fff;
    font-size: 12px;
    overflow: auto;
    padding:10px;
  }

  .stock>header{
    padding:10px 20px 50px;
  }

  .stock>header>p{
    color: #fff;
    font-size:16px;
  }

  .stock>header>nav{
    text-align: center;
    font-size:16px;
    /* font-weight: bold; */
    background-color:#206EC0;
    border:1px solid #5A94D1;
    border-radius:5px;
    padding:5px 0;
  }

  .stock>header>nav>span{
    padding:0 15px;
  }

  .stock>header>section{
    margin-top:15px;
    background-color:#206EC0;
    border:1px solid #5A94D1;
    border-radius:5px;
    padding:25px 15px; 
  }

  .stock>header>section>div,.stock>header>section>section>section{
    display: flex;
    justify-content: space-between;
  }

  .stock>header>section>section{
    padding: 10px 0;
    border-bottom: 1px solid #fff;
  }

  .stock>header>section{
    position: relative;
  }

  .stock>header>section>header{
    width: 0px;
    height: 0px;
    position: absolute;
  }

  .stock>header>section>header:first-child{
    border: 8px solid transparent;
    border-bottom-color: #206EC0;
    z-index:910;
    top:-16px;
  }

  .stock>header>section>header:nth-child(2){
    border: 10px solid transparent;
    border-bottom-color: #5A94D1;
    z-index:905;
    top:-20px;
  }  

  .left1{
    left:100px;
  }

  .left2{
    left:98px;
  }

  .right1{
    right:100px;
  }

  .right2{
    right:98px;
  }

  .pitchOn{
    font-weight: bold;
  }

  /*  */
  .aboutUs>header:first-child>img{
    width:30%;
    margin-top:30px;
  }

  .aboutUs>nav:nth-child(2){
    margin:30px 50px;
    border-bottom:1px solid #fff;
  }

  .aboutUs>p{
    text-align: justify;
    margin:0 50px;
    text-indent: 25.5px;
    color:#fff;
    letter-spacing:2px;
  }

  .aboutUs>footer{
    margin-top:150px;
  }

  /*  */
  .serve{
    color:#fff;
    padding:40px;
  }

  .serve>header{
    text-align: center;
    /* margin-top:30px; */
    border-bottom: 1px solid #fff;
    font-size:16px;
    padding-bottom:5px;
  }

  .serve>section{
    margin:30px 0;
  }

  .serve>section>p{
    font-size:12px;
    text-align: justify;
    text-indent: 25.5px;
    color:#fff;
    /* letter-spacing:2px; */
  }

  /*  */
  .account>section{
    padding:10px 30px;
  }

  .account>section input{
    background-color:transparent;
    border:0;
    border-bottom: 1px solid #fff;
    text-align: left;
  }

  .verify{
    position: relative;
  }

  .verify>span{
    position: absolute;
    right:30px;
    bottom:25px;
    color:#fff;
    font-size:12px;
  }

  .account>section input:last-child{
    background: #024F79;
    border-radius: 50px;
    padding: 10px;
    width: 100%;
    text-align: center;
    margin:10px 0;
    color:#fff;
    border:0;
  }

  .account>section h4{
    margin-bottom:50px;
    letter-spacing: 3px;
  }

  .earnings>ul>li{
    display: flex;
    justify-content: space-around;
    padding:10px 0;
  }

  .earnings>ul>li>span>p{
    color:#fff;
    margin-bottom:5px;
  }

  .earnings>ul>li>span>p:last-child{
    margin-bottom:0px;
  }

  .earnings>ul>li>span>p:last-child{
    font-size:12px;
  }

  .earnings>ul>li>span:nth-child(2)>p:last-child{
    color:blue;
    text-align: end;
  }

  .earnings{
    margin-bottom: 50px;
  }

  .earnings>ul>li:nth-of-type(even){
    background-color:rgba(2, 79, 121, .5);
  }
</style>



