<template>
  <div class="app">
    <!--  -->
    <div class="register" v-show="isShow == 1">
      <img src="../../img/Register/yitihua_logo.png" alt="">
      <p>Green Charge piles stations unify, It is a great practice to explore new energy charging industry and commercialization.</p>
      <button  @click="isShow = 2">&nbsp;&nbsp;&nbsp;</button>
    </div>
    <!--  -->
    <div class="welcome" v-show="isShow == 2">
      <a href="#"  @click="isShow = 3"> ></a>
    </div>
    <!--  -->
    <div class="phoneReg" v-show="isShow == 3">
      <img src="../../img/Phone/shortcut.png" alt="">
      <p></p>
      <input type="text" placeholder="" v-model="phone">
      <input type="button" value="" @click="securityCode" style="background-color:#24A4D1;border:#24A4D1;">
      <input type="button" value="" @click="isShow = 4">
      <!-- <label for="remmber">
        <input name="Fruit" type="checkbox" value="1" class="rember"/>
        <span></span><router-link to="#"></router-link><router-link to="#"></router-link>
      </label> -->
      <div >
        <span class="mui-icon mui-icon-checkmarkempty rember" v-show="checkbox" @click="checkBox"></span>
        <span class="rember" v-show="!checkbox" @click="checkBox"></span>
        <span @click="checkBox"></span>
        <router-link to="#"></router-link>
        <router-link to="#"></router-link>
      </div>
    </div>
    <!--  -->
    <div class="phoneReg" v-show="isShow == 4">
      <span class="mui-icon mui-icon-back arrow" @click="isShow = 3"></span>
      <img src="../../img/Phone/password.png" alt="">
      <!-- <p></p> -->
      <p></p>
      <form method="post" action="" >
        <input type="text" placeholder="" v-model="phone">
        <input type="password" placeholder="" v-model="password">
        <p @click="reset()"></p>
        <input type="button" value="" @click="passReg">
      </form>
      <div>
        <span class="mui-icon mui-icon-checkmarkempty rember" v-show="checkbox" @click="checkBox"></span>
        <span class="rember" v-show="!checkbox" @click="checkBox"></span>
        <span @click="checkBox"></span>
        <router-link to="#"></router-link>
        <router-link to="#"></router-link>
      </div>
    </div>
    <!--  -->
    <div class="phoneReg" v-show="isShow == 5">
      <span class="mui-icon mui-icon-back arrow" @click="isShow = 4"></span>
      <img src="../../img/Phone/reset.png" alt="">
      <p> <span v-html="phone2"></span></p>
      <form method="post" action="" >
        <input type="number" placeholder="" v-model="phone">
        <div class="verify">
          <input type="text" placeholder="" v-model="security">
          <span v-show="send" @click="countDown()"></span>
          <span v-html="time" v-show="!send">60</span>
        </div>
        <input type="password" placeholder="6-15" v-model="newPass">
        <input type="password" placeholder="" v-model="newPass2">
        <input type="button" value="" @click="resetPass">
      </form>
    </div>
    <!--  -->
    <div class="phoneReg" v-show="isShow == 6">
      <span class="mui-icon mui-icon-back arrow" @click="isShow = 3"></span>
      <img src="../../img/Phone/shortcut.png" alt="">
      <p> <span v-html="phone2"></span></p>
      <form method="post" action="" >
        <input type="number" placeholder="" v-model="phone">
        <div class="verify">
          <input type="text" placeholder="" v-model="security">
          <span v-show="send" @click="countDown()"></span>
          <span v-html="time" v-show="!send">60</span>
        </div>
        <input type="button" value="" @click=secuRge>
      </form>
    </div>    
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow:2,
      checkbox:false,
      phone:'',
      phone2:'',
      password:'',
      time:60,
      send:true,
      timer:'',
      security:'',
      newPass:'',
      newPass2:''
    }
  },
  mounted(){
    if(this.isShow == 2){
      setTimeout(()=>{
        this.isShow = 3;
      },3000);
    }
  },
  methods:{
    checkBox() {
      if(this.checkbox == true){
        this.checkbox = false;
      }else{
        this.checkbox = true;
      }
    },
    countDown() {
      this.phone2 = this.phone;
      this.send = false;
      this.security = '';
      this.time = 60;
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        if(this.time > 0){
          this.time--;
          // console.log(this.time);
        }else{
          clearInterval(this.timer);
          this.timer=null;
          this.send = true;
          this.time = 60;
          // console.log('???');
        }
      },1000);
    },  
    reset() {
      if((/^1[34578]\d{9}$/).test(this.phone)){
        this.isShow = 5;
        this.countDown();
        this.phone2 = this.phone;       
      }else{
        this.$toast({
            message:"11",
            position:"center",
            duration:2000
        });  
      }
    },
    passReg() {
      if(this.checkbox == true){
        if((/^1[34578]\d{9}$/).test(this.phone)){
          if((/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_\.-@]{6,15}$/).test(this.password)){
            this.$router.push("/home");
          }else{
            this.$toast({
                message:"6-15",
                position:"center",
                duration:2000
            });  
          }
        }else{
          this.$toast({
              message:"11",
              position:"center",
              duration:2000
          });  
        }
      }else{
        this.$toast({
            message:"",
            position:"center",
            duration:2000
        });  
      }
    },
    securityCode() {
      if(this.checkbox == true){
        if((/^1[34578]\d{9}$/).test(this.phone)){
          this.isShow = 6;
          this.countDown();
          this.phone2 = this.phone;
        }else{
          this.$toast({
              message:"11",
              position:"center",
              duration:2000
          });  
        }
      }else{
        this.$toast({
            message:"",
            position:"center",
            duration:2000
        });  
      }
    },
    secuRge() {
      if((/^\w{6}$/).test(this.security)){
        if((/^1[34578]\d{9}$/).test(this.phone)){
          this.$router.push("/home");
        }else{
          this.$toast({
              message:"11",
              position:"center",
              duration:2000
          });  
        }
      }else{
        this.$toast({
            message:"6",
            position:"center",
            duration:2000
        });  
      }      
    },
    resetPass() {
      if((/^\w{6}$/).test(this.security)){
        if((/^1[34578]\d{9}$/).test(this.phone)){
          if((/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_\.-@]{6,15}$/).test(this.newPass)){
            if(this.newPass == this.newPass2){
              this.isShow = 4;
              this.newPass = this.newPass2 = '';
              this.$toast({
                  message:"",
                  position:"center",
                  iconClass: 'mintui mintui-success',
                  duration:2000
              });
            }else{
              this.newPass = this.newPass2 = '';
              this.$toast({
                message:"",
                position:"center",
                iconClass: 'mintui mintui-success',
                duration:2000
              });
            }
          }else{
            this.$toast({
                message:"6-15",
                position:"center",
                duration:2000
            });  
          }
        }else{
          this.$toast({
              message:"11",
              position:"center",
              duration:2000
          });  
        }
      }else{
        this.$toast({
            message:"6",
            position:"center",
            duration:2000
        });  
      }
    }
  }
}
</script>


<style scoped>
  .app{
    height:100%;
  }

  .register,.welcome,.phoneReg{
    height:100%;
    background-repeat: no-repeat;
    background-size:contain;
    text-align: center;
  }

  .register{
    background-image: url("../../img/Register/background_login.jpg");
  }

  .welcome{
    background-image: url("../../img/Start/gif_donghua.gif");
    position: relative;
  }

  .phoneReg{
    background-image: url("../../img/Phone/background.jpg");
  }

  .register>img,.welcome>img{
    width: 50%;
    margin:170px 0 135px 0;
  }

  .phoneReg>img{
    width: 70%;
    margin:200px 0 10px 0;
  }

  .register>p{
    display: inline-block;
    color:#fff;
    width:80%;
    text-align: center;
    margin-bottom:80px;
  }

  .register>button{
    border-radius:20px;
    width:150px;
    color:#2D74A2;
    font-size:16px;
    font-weight:550;
  }

  .phoneReg>div:last-child{
    height:20px;
    display: flex;
    justify-content: center;
    align-content: center;
    margin-top:15px;
    color:#fff;
    font-family: 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 12px;    
  }

  .welcome>a{
    display: inline-block;
    color:#fff;
    position: fixed;
    bottom:150px;
    right:50px;
  }

  /*  */
/* label>.remmber {
	display: none;
}

label>.remmber[type=checkbox]+span {
	display: inline-block;
	border-radius: .05rem;
	width: .28rem;
	height: .28rem;
	border: 1px solid #146395;
	color: #146395;
	position: absolute;
	bottom: 170px;
  left: 20px;
  padding:8px;
}

label>.remmber[type=checkbox]:checked+span:after {
	content: '\2714';
	position: absolute;
	font-size: 0.28rem;
  left: 15px;
  top:50px;
} */

.phoneReg>label{
  display: block;
  font-size:12px;
  color:#fff;
}

/*  */
  .phoneReg input{
    width:70%;
    border-radius:50px;
    background:transparent;
    border:1px solid #fff;
    color:#fff;
    font-family: 'Helvetica Neue', Helvetica, sans-serif;
    font-size: 14px;
    margin-bottom: 15px;
  }

  .phoneReg>p,.phoneReg>form>p{
    color:#fff;
  }

  .phoneReg>p:nth-child(2){
    margin-bottom:50px;
  }

  .app>div:nth-child(3)>input:nth-child(5){
    background-color:#136396;
    border:1px solid #136396;
  }

  .app>div:nth-child(4)>form>input[type='button'],.app>div:nth-child(5)>input[type='button'],form>input[type='submit'],form>input[type='button']{
    background-color:#136396;
    border:1px solid #136396;
  }
  
  .app>div:nth-child(4)>form>p:nth-child(3){
    text-align: right;
    font-size:12px;
    margin-right:50px;
  }

  .rember{
    border:1px solid #146395;
    display: inline-block;
    width:18px;
    height:18px;
    font-size: 16px;
    color:#146395;
    margin-right:5px;
    overflow: hidden;
  }

  .phoneReg>div:last-child>a{
    color:#146395;
  }

  .verify{
    position: relative;
  }

  .verify>span{
    position: absolute;
    right:70px;
    bottom:25px;
    color:#fff;
    font-size:12px;
  }

  .phoneReg>.arrow{
    position: fixed;
    top:15px;
    left:15px;
    color:#fff;
  }

  .app>div:nth-child(5)>p:nth-child(3){
    margin-bottom:50px;
  }

  .app>div input[type='button'],form>input[type='submit']{
    height:40px;
  }
</style>



