<!----id-->
<template>
    <div class="box">
        <!---->
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="item in imagelist" :key="item.id">
                <img :src="item.img_url">
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
    export default{
        data(){
            return{}
        },
        methods:{

        },
        props:["imagelist"]//imagelist
    }
</script>

<style>
    /**/
    .box .mint-swipe{
        height: 149px;
    }
    /**/
    .box .mint-swipe .mint-swipe-item{
        text-align:center;
    }
    /*  */
    .box .mint-swipe .mint-swipe-item img{
        width:100%;
        height:100%;
    }

    .mint-swipe-indicators{
        display: none;
    }
</style>


